import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


export default new Vuex.Store({
    actions: {
        record(context, value) {
            context.commit('RECORD', value)
        }
    },
    mutations: {
        RECORD(state, value) {
            state.fill = value
        }
    },
    state: {
        fill: JSON.stringify(new Array(9).fill(1).map(() => new Array(9))),
        test: 'hello'
    }
})

