import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Sodoku from '../components/Sodoku.vue'
import Summon from '../components/Summon.vue'
import Typing from '../components/Typing.vue'
import Input from '../components/typing/Input.vue'
import Result from '../components/typing/Result.vue'
import Settings from '../components/typing/Settings.vue'

export default new VueRouter({
    routes: [
        {
            path: '/sodoku',
            component: Sodoku
        },
        {
            path: '/summon',
            component: Summon
        },
        {
            path: '/typing',
            component: Typing,
            children: [
                {
                    path: '',
                    redirect: 'input'
                },
                {
                    path: 'input',
                    name: 'input',
                    component: Input
                },
                {
                    path: 'result',
                    name: 'result',
                    component: Result,
                    props($route) {
                        return {
                            inputTime: $route.params.inputTime,
                            inputValue: $route.params.inputValue,
                            standardAnswer: $route.params.standardAnswer
                        }
                    }
                },
                {
                    path: 'settings',
                    name: 'settings',
                    component: Settings,
                }
            ]
        },
    ]
})
