<template>
    <div class="central">
        <div class="inputBoard" @mouseleave="clearHint">
            <div class="row" v-for="(item, i) in fill" :key="i">
                <div
                    class="cell"
                    v-for="(cell, j) in item"
                    :key="j"
                    @mouseover="nextHint(i, j)"
                >
                    <input
                        v-model.number="fill[i][j]"
                        type="text"
                        @blur="validCheck(i, j, $event)"
                        :disabled="disabled[i][j]"
                    />
                    <div class="hintPanel">
                        <div class="hintNumber" v-for="(item,i) in hint" :key="i">
                            {{ item }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="remote">
            <div
                v-show="!onTiming"
                class="timing remoteButton"
                @click="startTiming"
            >
                <div class="lockIcon">
                    <i class="iconfont">&#xe614;</i>
                </div>
                <div class="lockInfo">开始计时</div>
            </div>

            <div
                v-show="onTiming"
                class="timing remoteButton"
                @click="endTiming"
            >
                <div class="lockIcon">
                    <i class="iconfont">&#xe626;</i>
                </div>

                <div class="lockInfo">
                    {{ passedTime }}
                </div>
            </div>

            <div class="problemLock remoteButton" @click="lock">
                <div class="lockIcon">
                    <i class="iconfont">&#xe621;</i>
                </div>
                <div class="lockInfo">锁定输入值</div>
            </div>

            <div class="problemUnlock remoteButton" @click="unlock">
                <div class="lockIcon">
                    <i class="iconfont">&#xe620;</i>
                </div>
                <div class="lockInfo">解锁输入值</div>
            </div>

            <div class="draftErase remoteButton" @click="erase">
                <div class="lockIcon">
                    <i class="iconfont">&#xe716;</i>
                </div>
                <div class="lockInfo">清除未锁定</div>
            </div>
            <div class="allErase remoteButton" @click="unlock(), clearAll()">
                <div class="lockIcon">
                    <i class="iconfont">&#xe62c;</i>
                </div>
                <div class="lockInfo">新题目</div>
            </div>
        </div>
    </div>
</template>
<script>
import Vue from "vue"
import { Input, Message } from "element-ui"
Vue.use(Input)
export default {
    data() {
        return {
            fill: new Array(9).fill(null).map(() => new Array(9).fill(null)),
            disabled: new Array(9)
                .fill(null)
                .map(() => new Array(9).fill(false)),
            hint: null,
            onTiming: false,
            passedSeconds: 0
        }
    },
    computed: {
        passedTime() {
            let seconds =
                this.passedSeconds % 60 < 10
                    ? "0" + (this.passedSeconds % 60)
                    : this.passedSeconds % 60
            let minutes =
                Math.floor(this.passedSeconds / 60) % 60 < 10
                    ? "0" + (Math.floor(this.passedSeconds / 60) % 60)
                    : Math.floor(this.passedSeconds / 60) % 60
            let hours = Math.floor(this.passedSeconds / 3600)
            return `${hours}:${minutes}:${seconds}`
        }
    },
    methods: {
        clearHint() {
            this.hint = null
        },
        nextHint(i, j) {
            if (this.isValid(this.fill[i][j])) {
                this.hint = null
                return
            }
            let forbidden = new Array()
            for (let k = 0; k < 9; k++) {
                if (k != j && this.isValid(this.fill[i][k]))
                    forbidden.push(this.fill[i][k])
                if (k != i && this.isValid(this.fill[k][j]))
                    forbidden.push(this.fill[k][j])
            }
            for (let k = 0; k < 3; k++) {
                for (let m = 0; m < 3; m++) {
                    let row = Math.floor(i / 3) * 3 + k
                    let col = Math.floor(j / 3) * 3 + m
                    if (
                        !(row === i && col === j) &&
                        this.isValid(this.fill[row][col])
                    )
                        forbidden.push(this.fill[row][col])
                }
            }
            let options = new Array()
            for (let k = 0; k < 9; k++) {
                if (forbidden.indexOf(k + 1) === -1) options.push(k + 1)
            }
            this.hint = options.sort()
        },
        isValid(cellContent) {
            return [1, 2, 3, 4, 5, 6, 7, 8, 9].indexOf(cellContent) !== -1
        },
        validCheck(i, j, event) {
            if (
                this.fill[i][j] === "" ||
                this.fill[i][j] === null ||
                this.fill[i][j] === undefined
            )
                return
            if (!this.isValid(this.fill[i][j])) {
                Message.error(this.fill[i][j] + " 不对呀，重新输一个吧")
                this.$set(this.fill[i], j, null)
                event.currentTarget.focus()
                return
            }

            let rowDuplicate = false
            let colDuplicate = false
            let blockDuplicate = false

            for (let k = 0; k < 9; k++) {
                if (j != k && this.fill[i][k] === this.fill[i][j]) {
                    rowDuplicate = true
                }
            }

            for (let k = 0; k < 9; k++) {
                if (i != k && this.fill[k][j] === this.fill[i][j]) {
                    colDuplicate = true
                }
            }

            for (let k = 0; k < 3; k++) {
                for (let m = 0; m < 3; m++) {
                    let row = Math.floor(i / 3) * 3 + k
                    let col = Math.floor(j / 3) * 3 + m
                    if (
                        this.fill[row][col] === this.fill[i][j] &&
                        (row !== i || col !== j)
                    ) {
                        blockDuplicate = true
                    }
                }
            }

            if (rowDuplicate || colDuplicate || blockDuplicate) {
                let duplicatePosition = new Array()
                if (rowDuplicate) duplicatePosition.push("这一行")
                if (colDuplicate) duplicatePosition.push("这一列")
                if (blockDuplicate) duplicatePosition.push("这个九宫格")
                let duplicateMessage = `${
                    this.fill[i][j]
                } 在${duplicatePosition.join("、")}里面已经有啦`
                Message.error(duplicateMessage)
                this.$set(this.fill[i], j, null)
                event.currentTarget.focus()
            } else {
                this.$store.dispatch("record", JSON.stringify(this.fill))
            }
        },

        startTiming() {
            this.onTiming = true
            this.timer = setInterval(() => {
                this.passedSeconds++
            }, 1000)
        },
        endTiming() {
            this.onTiming = false
            this.passedSeconds = 0
            clearInterval(this.timer)
        },
        lock() {
            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    this.$set(
                        this.disabled[i],
                        j,
                        this.isValid(this.fill[i][j])
                    )
                }
            }
        },
        unlock() {
            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    this.$set(this.disabled[i], j, false)
                }
            }
        },
        erase() {
            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    if (!this.disabled[i][j]) {
                        this.$set(this.fill[i], j, null)
                    }
                }
            }
            this.$store.dispatch("record", JSON.stringify(this.fill))
        },
        clearAll() {
            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    this.$set(this.fill[i], j, null)
                }
            }
        }
    }
}
</script>


<style scoped>
* {
    margin: 0px;
    padding: 0px;
}
.central {
    display: flex;
    flex-direction: column;
    width: 360px;
    height: 720px;
    border-width: 0px;
    /* background-color: blueviolet; */
}
.inputBoard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 360px;
    height: 360px;
    border-width: 0px;
    background-color: lightsalmon;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 360px;
    height: 38px;
}

.cell {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 38px;
    height: 38px;
    justify-content: center;
    align-items: center;
    font-size: 2em;
}

.cell:hover {
    background-color: #dbec8d;
    /* background-color: #b3d8ff; */
    border-radius: 6px;
}

input {
    width: 30px;
    height: 30px;
    text-align: center;
    font-size: 1em;
    border-radius: 2px;
    border-width: 0px;
    outline: none;
    transition-duration: 0.5s;
    transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
}

input:disabled {
    width: 30px;
    height: 30px;
    text-align: center;
    font-size: 1em;
    border-width: 0px;
    color: rgb(95, 145, 202);
    cursor: not-allowed;
}

.cell .hintPanel,
.cell:hover input:disabled + .hintPanel {
    position: absolute;
    display: none;
    background-color: #fff;

    transition: background-color 0.5s ease-out;
    visibility: hidden;
}

.cell:hover .hintPanel {
    display: flex;
    position: absolute;
    flex-direction: row;
    visibility: visible;
    background-color: green;
    color:whitesmoke;
    border-radius: 0px 10px 10px 10px;
    width: 250px;
    height: 50px;
    top: 38px;
    left: 0px;
    z-index: 2;
    font-size: 1em;
    white-space: nowrap;
}

.cell:hover .hintPanel:hover {
    background-color: rgb(252, 120, 68);
    display: flex;
    flex-direction: row;
}
.hintNumber{
    width: 40px;
    height: 40px;
}

.remote {
    display: flex;
    flex-direction: column;
    width: 360px;
    height: 360px;
    border-width: 0px;
    color: rgb(20, 20, 20);
    color: #62bfad;
    font-size: 2em;
}

.remoteButton {
    display: flex;
    flex-direction: row;
    height: 20%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: #f0eed6;
    transition: 1s cubic-bezier(0.03, 1.15, 0.66, 0.92);
}
.remoteButton:hover {
    background-color: #f9f7e8;
}
.remoteButton:active {
    background-color: #d1cfb9;
}

.remote .lockInfo {
    width: 50%;
    font-size: 30px;
    text-align: center;
}

.iconfont {
    font-family: "iconfont" !important;
    line-height: 90px;
    font-style: normal;
    font-size: 40px;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}
@font-face {
    font-family: "iconfont"; /* Project id 2697342 */
    src: url("//at.alicdn.com/t/font_2697342_6qyg67ntydi.woff2?t=1629540648712")
            format("woff2"),
        url("//at.alicdn.com/t/font_2697342_6qyg67ntydi.woff?t=1629540648712")
            format("woff"),
        url("//at.alicdn.com/t/font_2697342_6qyg67ntydi.ttf?t=1629540648712")
            format("truetype");
}
</style>