<template>
    <div class="mark">
        <div
            class="peek"
            v-for="(item, i) in mark"
            :key="i"
            @mouseover="reveal(item)"
            @mouseout="revealHide"
        >
            {{ item }}
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex"
export default {
    data() {
        return {
            mark: [1, 2, 3, 4, 5, 6, 7, 8, 9],
            occupied : new Array(9)
                .fill(1)
                .map(() => new Array(9).fill(false))
        }
    },
    computed: {
        ...mapState({
            fill: (state) => {
                return JSON.parse(state.fill)
            }
        })
    },
    methods: {
        isValid(cellContent) {
            return [1, 2, 3, 4, 5, 6, 7, 8, 9].indexOf(cellContent) !== -1
        },
        reveal(number) {

            let locker = this.$el.parentNode.firstChild.firstChild
            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    let rowDuplicate = false
                    let colDuplicate = false
                    let blockDuplicate = false

                    for (let k = 0; k < 9; k++) {
                        if (this.fill[i][k] === number) {
                            rowDuplicate = true
                        }
                    }

                    for (let k = 0; k < 9; k++) {
                        if (this.fill[k][j] === number) {
                            colDuplicate = true
                        }
                    }

                    for (let k = 0; k < 3; k++) {
                        for (let m = 0; m < 3; m++) {
                            let row = Math.floor(i / 3) * 3 + k
                            let col = Math.floor(j / 3) * 3 + m
                            if (this.fill[row][col] === number) {
                                blockDuplicate = true
                            }
                        }
                    }

                    this.occupied[i][j] =
                        rowDuplicate ||
                        colDuplicate ||
                        blockDuplicate ||
                        this.isValid(this.fill[i][j])
                }
            }

            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    if (this.occupied[i][j] == false) {
                        locker.childNodes[i].childNodes[
                            j
                        ].firstChild.style.backgroundColor = "rgb(24,232,38)"
                    } else {
                        locker.childNodes[i].childNodes[
                            j
                        ].firstChild.style.backgroundColor = "rgb(252,91,80)"
                    }
                }
            }
        },
        revealHide() {
            let locker = this.$el.parentNode.firstChild.firstChild
            for (let i = 0; i < 9; i++) {
                for (let j = 0; j < 9; j++) {
                    locker.childNodes[i].childNodes[
                        j
                    ].firstChild.style.backgroundColor = null
                }
            }
        }
    }
}
</script>

<style scoped>
* {
    margin: 0px;
    padding: 0px;
}
.mark {
    display: flex;
    flex-direction: column;
    width: 160px;
    height: 720px;
    border-width: 0px;
    background-color: #7bed9f;
}
.peek {
    width: 160px;
    height: 80px;
    border-width: 0px;
    font-size: 2em;
    text-align: center;
    line-height: 80px;
    background-color: cornsilk;
    color: purple;
    border-radius: 40px;
    transition: all 400ms;
}

.peek:nth-child(odd) {
    background-color: #ff7f50;
}

.peek:hover {
    background-color: rgb(159, 220, 245);
    color: rgb(100, 98, 223);
}
</style>