<template>
    <div class="central">
        <div class="resultInfo">
            <div class="resultDetail">
                <div class="resultName">完成时间</div>
                <div class="resultValue">{{ timeUsed }}</div>
            </div>
            <div class="resultDetail">
                <div class="resultName">正确率</div>
                <div class="resultValue">{{ correctRate }}</div>
            </div>
            <div class="resultDetail">
                <div class="resultName">WPM</div>
                <div class="resultValue">{{ wpm }}</div>
            </div>
        </div>
        <el-button type="primary" @click="newGame">再来一局</el-button>
    </div>
</template>

<script>
import Vue from "vue"
import { Message, Button } from "element-ui"
Vue.use(Button)
export default {
    name: "result",
    props: ["inputValue", "inputTime", "standardAnswer"],
    computed: {
        timeUsed() {
            return this.inputTime / 1000 + "s"
        },
        correctLetter() {
            let correctLetter = 0
            for (let i = 0; i < this.standardAnswer.length; i++) {
                if (this.standardAnswer[i] === this.inputValue[i])
                    correctLetter++
            }
            return correctLetter
        },
        correctRate() {
            let rate = this.correctLetter / this.standardAnswer.length
            return Math.floor(rate * 10000) / 100 + "%"
        },
        wpm() {
            let wpm = this.correctLetter / 5 / (this.inputTime / 60000)
            return Math.floor(wpm * 100) / 100
        }
    },
    data() {
        return {
            first: null,
            second: null
        }
    },
    methods: {
        newGame() {
            this.$router.replace({
                path: "input"
            })
        },
        sayhello() {
            Message.success("hello")
        }
    }
}
</script>

<style scoped>
.central {
    width: 1200px;
    background-color: rgb(218, 233, 252);
}
.resultInfo {
    display: flex;
    width: 100%;
    height: 200px;
    justify-content: space-around;
}
.resultDetail {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    width: 200px;
    height: 100px;
    font-size: 3em;
}
</style>