<template>
    <div class="central">
        <div class="settingPanel">
            <div class="titleBar">Enjoy Typing</div>
            <el-button type="primary" icon="el-icon-setting" @click="settings"
                >自定义设置</el-button
            >
        </div>
        <hr />
        <div class="typingBoard" @click="startInput">
            <div
                class="wordBox"
                v-for="(word, i) in wordListWithSpace"
                :key="i"
            >
                <div class="word">
                    {{ word === " " ? "&nbsp;" : word }}
                </div>
                <div class="letters">
                    <div
                        class="letter"
                        v-for="(letter, j) in wordString(word)"
                        :key="j"
                    >
                        {{ letter }}
                    </div>
                </div>
            </div>
        </div>

        <input v-model="userInput" @blur="underlineHide" type="text" />
    </div>
</template>

<script>
import Vue from "vue"
import { Message, Input, Button } from "element-ui"
import dayjs from "dayjs"
import { pinyin } from "pinyin-pro"
Vue.use(Input)
Vue.use(Button)
export default {
    name: "input-board",
    props: ["wordList", "markerMode"],
    data() {
        return {
            textarea: null,
            userInput: "",
            inputStart: null,
            nextWordIndex: 0,
            nextLetterIndex: 0
        }
    },
    computed: {
        wordListWithSpace() {
            return this.wordList.join("| |").split("|")
        },
        letterList() {
            return this.wordList.map((word) =>
                pinyin(word, {
                    toneType: "none",
                    type: "array"
                }).join("")
            )
        },
        standardAnswer() {
            return this.letterList.join(" ")
        },
        wordLettersCount() {
            let counter = 0
            return this.letterList
                .join("| |")
                .split("|")
                .map((word) => {
                    if (word === " ") {
                        counter += 1
                        return counter
                    }
                    counter += word.length
                    return counter
                })
        }
    },
    watch: {
        userInput(newValue, oldValue) {
            // record the moment of start
            if (oldValue.length == 0 && newValue.length > 0) {
                this.inputStart = dayjs()
            }
            // when finished last letter, send data and jump to result
            if (newValue.length >= this.standardAnswer.length) {
                this.$router.replace({
                    name: "result",
                    params: {
                        inputValue: this.userInput,
                        standardAnswer: this.standardAnswer,
                        inputTime: dayjs().diff(this.inputStart, "milliseconds")
                    }
                })
                return
            }
            // clear previous underline
            this.underlineHide()
            let position = this.locatePosition(newValue.length)
            this.nextWordIndex = position[0]
            this.nextLetterIndex = position[1]
            this.typeCheck(oldValue.length, newValue.length)

            this.underlineShow()
        }
    },
    mounted() {
        this.typeCheck(0, this.standardAnswer.length)
    },
    methods: {
        settings() {
            this.$router.replace({
                name: "settings"
            })
        },
        wordString(word) {
            if (word === " ") return word
            return pinyin(word, { toneType: "none", type: "array" })
                .join("")
                .split("")
        },
        locatePosition(stringIndex) {
            let wordIndex = this.wordLettersCount
                .map((sentenceLength) => {
                    return stringIndex < sentenceLength
                })
                .indexOf(true)
            let nextWordLetters =
                this.wordListWithSpace[wordIndex] === " "
                    ? 1
                    : pinyin(this.wordListWithSpace[wordIndex], {
                          toneType: "none",
                          type: "array"
                      }).join("").length

            let letterIndex =
                stringIndex -
                (this.wordLettersCount[wordIndex] - nextWordLetters)
            return [wordIndex, letterIndex]
        },
        test() {
            return pinyin("号") + Message("good") + dayjs()
        },
        startInput() {
            this.$el.childNodes[3].focus()
            this.underlineShow()
        },
        underlineShow() {
            let nextLetterDiv =
                this.$el.childNodes[2].childNodes[this.nextWordIndex]
                    .childNodes[1].childNodes[this.nextLetterIndex]
            if (this.markerMode === "caret")
                nextLetterDiv.style.setProperty("--caret-display", "block")
            else if (this.markerMode === "underline")
                nextLetterDiv.style.setProperty("--underline-display", "block")
        },
        underlineHide() {
            let nextLetterDiv =
                this.$el.childNodes[2].childNodes[this.nextWordIndex]
                    .childNodes[1].childNodes[this.nextLetterIndex]
            if (this.markerMode === "caret")
                nextLetterDiv.style.setProperty("--caret-display", "none")
            else if (this.markerMode === "underline")
                nextLetterDiv.style.setProperty("--underline-display", "none")
        },
        typeCheck(from, to) {
            for (let i = from; i < to; i++) {
                let [wordIndex, letterIndex] = this.locatePosition(i)
                let checkDiv =
                    this.$el.childNodes[2].childNodes[wordIndex].childNodes[1]
                        .childNodes[letterIndex]
                if (this.markerMode === "underline") {
                    if (i >= this.userInput.length) {
                        checkDiv.style.backgroundColor = "white"
                    } else if (this.userInput[i] === this.standardAnswer[i]) {
                        checkDiv.style.backgroundColor = "#e7fbd3"
                        checkDiv.style.color = "#0e630e"
                    } else if (this.userInput[i] !== this.standardAnswer[i]) {
                        checkDiv.style.backgroundColor = "pink"
                        checkDiv.style.color = "darkred"
                    }
                } else if (this.markerMode === "caret") {
                    checkDiv.style.backgroundColor = "lightsteelblue"
                    if (i >= this.userInput.length) {
                        checkDiv.style.color = "#0e630e"
                    } else if (this.userInput[i] === this.standardAnswer[i]) {
                        checkDiv.style.color = "#8cfc1c"
                    } else if (this.userInput[i] !== this.standardAnswer[i]) {
                        checkDiv.style.color = "#ab1cfc"
                    }
                }
            }
        },
        pauseInput() {
            if (this.userInput.length >= this.wordString.length) return
        }
    }
}
</script>

<style scoped>
input {
    position: fixed;
    left: -500px;
}
hr {
    background-color: rgb(241, 255, 219);
    height: 10px;
    border: none;
}
.titleBar{
    line-height: 60px;
    font-size: 2.5em;
    padding-left: 30px;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier,
        monospace;
}
.central {
    width: 1200px;
    background-color: rgb(218, 233, 252);
}
.typingBoard {
    display: flex;
    flex-wrap: wrap;
    height: 540px;
    padding: 30px;
}
.settingPanel {
    display: flex;
    justify-content: space-between;
}
.wordBox {
    height: 100px;
}
.word {
    background-color: rgb(255, 198, 176);
    text-align: center;
    height: 50px;
    font-size: 2em;
    line-height: 50px;
    color: #3b3b3b;
}
.letters {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.letter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15px;
    height: 40px;
    font-size: 2em;
    border-radius: 4px;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier,
        monospace;
    position: relative;
    color: #505050;
    --underline-display: none;
    --caret-display: none;
}
.letter:nth-child(n)::before {
    content: " ";
    position: absolute;
    left: -1px;
    top: 0;
    width: 3px;
    height: 40px;
    display: var(--caret-display);
    animation: firefly 1s infinite cubic-bezier(0, 0.89, 0.41, 1.06);
}
.letter:nth-child(n)::after {
    content: " ";
    position: absolute;
    left: 0;
    bottom: -6px;
    width: 15px;
    height: 3px;
    background-color: blueviolet;
    display: var(--underline-display);
}
@keyframes firefly {
    0% {
        background-color: rgba(242, 245, 71, 0);
    }
    50% {
        background-color: rgba(242, 245, 71, 1);
    }
    100% {
        background-color: rgba(242, 245, 71, 0);
    }
}
</style>