<template>
    <div>
        <router-view
            :wordList="wordList"
            :markerMode="markerMode"
            @edit="editWordList"
            @modeSwitch="editMode"
        ></router-view>
    </div>
</template>

<script>
export default {
    name: "typing",
    data() {
        return {
            wordList: ["苹果", "橘子", "香蕉"],
            markerMode: "underline"
        }
    },
    methods: {
        editWordList(val) {
            this.wordList = val
        },
        editMode(val) {
            this.markerMode = val ? "underline" : "caret"
        }
    }
}
</script>

