import Vue from 'vue'
import App from './App.vue'
import Message from 'element-ui'
import store from './store'
import VueRouter from 'vue-router'
//引入路由器
import router from './router'

Vue.config.productionTip = false
Vue.prototype.$message = Message
Vue.use(VueRouter)

new Vue({
  el: '#app',
  render: h => h(App),
  store,
  router
}).$mount('#app')
