<template>
    <div class="central">
        <div class="titleBar">自定义设置</div>
        <hr />
        <div class="settingArea">
            <el-form
                ref="form"
                :label-position="labelPosition"
                label-width="100px"
            >
                <el-form-item label="自定义词组">
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 5, maxRows: 15 }"
                        style="width: 500px"
                        placeholder="请输入内容"
                        v-model="textarea"
                    >
                    </el-input>
                </el-form-item>
                <el-form-item label="提示符模式">
                    <el-switch
                        v-model="underlineMode"
                        active-color="#8A2BE2"
                        inactive-color="#F2F547"
                        active-text="下划线模式"
                        inactive-text="光标模式"
                    >
                    </el-switch>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="returnInput"
                        >保存并返回打字模式</el-button
                    >
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import Vue from "vue"
import { Button, Form, FormItem, Switch } from "element-ui"
Vue.use(Button)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Switch)
export default {
    name: "settings",
    props: ["wordList", "markerMode"],
    data() {
        return {
            textarea: this.wordList.join("|"),
            underlineMode: this.markerMode === "underline",
            labelPosition: "right"
        }
    },
    computed: {
        newWordList() {
            return this.textarea.split("|")
        }
    },
    methods: {
        returnInput() {
            this.$router.replace({
                name: "input"
            })
            this.$emit("edit", this.newWordList)
            this.$emit("modeSwitch", this.underlineMode)
        }
    }
}
</script>

<style scoped>
.central {
    width: 1200px;
    background-color: rgb(218, 233, 252);
}
hr {
    background-color: rgb(241, 255, 219);
    height: 10px;
    border: none;
}
.titleBar{
    line-height: 60px;
    font-size: 2em;
    padding-left: 30px;
}
.settingArea{
    padding: 30px;
}
</style>